<template>
  <div v-if="loading" class="">
    <LoadSkeletonSearch></LoadSkeletonSearch>
  </div>
  <div v-else class="content">
    <div class="bg-search">
      <div
        style="
          background-image: url('https://wework.qpic.cn/wwpic3az/685293_LOkThzY1SWuVRbh_1707191742/0');
        "
        class="py-24 px-4 rounded-lg mx-auto text-center bg-center bg-cover bg-no-repeat dark:bg-gray-700 bg-gray-700 bg-blend-multiply"
      >
        <div class="w-1/2 search-input mx-auto">
          <fwb-input
            v-model="q"
            size="lg"
            class="rounded-sm"
            placeholder="搜索"
            @keyup.enter="searchRef()"
          >
            <template #prefix>
              <svg
                aria-hidden="true"
                class="w-8 h-8 text-gray-500 dark:text-gray-400"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                />
              </svg>
            </template>
          </fwb-input>
        </div>
      </div>
    </div>
    <div class="search-content">
      <div class="content-video-title flex items-center justify-between py-6">
        <div class="content-video-title-lift flex items-center justify-between">
          <div class="title-icon">
            <svg
              class="w-6 h-6 text-gray-800 dark:text-white"
              aria-hidden="true"
              xmlns="http://www.w3.org/2000/svg"
              fill="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                fill-rule="evenodd"
                d="M5 10.2c.6 0 1 .4 1 1v.2l6 3.5 6-3.5v-.2a1 1 0 1 1 2 0v.8c0 .4-.2.7-.5.9l-7 4a1 1 0 0 1-1 0l-7-4A1 1 0 0 1 4 12v-.8c0-.6.5-1 1-1Zm0 5c.6 0 1 .4 1 1v.2l6 3.4 6-3.4V16a1 1 0 1 1 2 0v.9c0 .3-.2.6-.5.8l-7 4a1 1 0 0 1-1 0l-7-4A1 1 0 0 1 4 17v-1c0-.5.4-1 1-1Z"
                clip-rule="evenodd"
              />
              <path
                d="M12.5 2.1a1 1 0 0 0-1 0l-7 4a1 1 0 0 0 0 1.8l7 4c.3.2.7.2 1 0l7-4a1 1 0 0 0 0-1.7l-7-4Z"
              />
            </svg>
          </div>
          <div class="title-text font-bold text-gray-800 dark:text-white">
            资源
          </div>
        </div>
      </div>
      <div class="content-video-list grid grid-cols-3 md:grid-cols-8 gap-3">
        <router-link
          :to="'/watch/' + line.ID"
          v-for="(line, number) in data"
          :key="number"
          class="video-item-card h-60 brightness-50 w-full rounded-sm object-cover object-center"
        >
          <img
            loading="lazy"
            class="video-item-card-image rounded-sm brightness-50"
            :src="line.image"
            alt=""
          />
          <div
            class="video-item-card-title mt-2 text-sm font-bold text-gray-800 dark:text-white"
          >
            {{ line.title }}
          </div>
          <div
            class="video-item-card-heard px-1 py-1 w-full absolute top-0 left-0"
          >
            <div
              class="item-card-heard-content flex items-center justify-between"
            >
              <div class="item-card-heard-speed text-sm text-gray-200">
                {{ COMMON.getSpeed(line) }}
              </div>
              <div class="item-card-heard-score text-sm text-yellow-300">
                {{ line.score + "分" }}
              </div>
            </div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { useToast, FwbInput } from "flowbite-vue";
import { ref, onMounted, getCurrentInstance } from "vue";
import LoadSkeletonSearch from "../widgets/LoadSkeletonSearch.vue";

export default {
  components: {
    LoadSkeletonSearch,
    FwbInput,
  },
  setup() {
    const data = ref(null);
    const q = ref(null);
    const loading = ref(true);
    const searching = ref(false);
    const toast = useToast();
    const error = ref(null);
    const { proxy } = getCurrentInstance();

    function fetchData() {
      let api = `${proxy.COMMON.apiUrl}/v1/api/video/jojo/list?page=1&size=24`;
      fetch(api, {
        method: "post",
        headers: {
          "content-type": "application/json",
          Authorization: localStorage.getItem("Authorization"),
        },
      })
        .then((res) => {
          if (!res.ok) {
            const error = new Error(res.statusText);
            error.json = res.json();
            throw error;
          }
          return res.json();
        })
        .then((json) => {
          if (json.code == 200) {
            data.value = json.data;
          } else if (json.code == 402) {
            toast.add({
              type: "success",
              time: 3000,
              text: "刷新token成功!",
            });
            localStorage.setItem("Authorization", json.data);
          } else if (json.code == 403) {
            toast.add({
              type: "warning",
              time: 3000,
              text: "登录失效,请重新登录!",
            });
            proxy.COMMON.loginOut();
          } else {
            toast.add({
              type: "success",
              time: 3000,
              text: json.msg,
            });
          }
        })
        .catch((err) => {
          error.value = err;
          if (err.json) {
            return err.json.then((json) => {
              error.value.message = json.message;
              toast.add({
                type: "success",
                time: 3000,
                text: error.value.message,
              });
            });
          }
        })
        .then(() => {
          loading.value = false;
        });
    }

    function searchData() {
      if (searching.value) {
        return;
      }
      searching.value = true;
      if (q.value.length === 0) {
        toast.add({
          type: "warning",
          time: 3000,
          text: "搜索词不能为空!",
        });
        return;
      }
      let api = `${proxy.COMMON.apiUrl}/v1/api/video/jojo/search?q=${q.value}`;
      fetch(api, {
        method: "post",
        headers: {
          "content-type": "application/json",
          Authorization: localStorage.getItem("Authorization"),
        },
      })
        .then((res) => {
          if (!res.ok) {
            const error = new Error(res.statusText);
            error.json = res.json();
            throw error;
          }
          return res.json();
        })
        .then((json) => {
          if (json.code == 200) {
            if (json.data.length === 0) {
              toast.add({
                type: "success",
                time: 3000,
                text: "未查询到相关内容!",
              });
            } else {
              data.value = json.data;
              toast.add({
                type: "success",
                time: 3000,
                text: "查询成功!",
              });
            }
          } else if (json.code == 402) {
            toast.add({
              type: "success",
              time: 3000,
              text: "刷新token成功!",
            });
            localStorage.setItem("Authorization", json.data);
          } else if (json.code == 403) {
            toast.add({
              type: "warning",
              time: 3000,
              text: "登录失效,请重新登录!",
            });
            proxy.COMMON.loginOut();
          } else {
            toast.add({
              type: "success",
              time: 3000,
              text: json.msg,
            });
          }
        })
        .catch((err) => {
          error.value = err;
          if (err.json) {
            return err.json.then((json) => {
              error.value.message = json.message;
              toast.add({
                type: "success",
                time: 3000,
                text: error.value.message,
              });
            });
          }
        })
        .then(() => {
          searching.value = false;
        });
    }
    onMounted(() => {
      document.title = "搜索";
      fetchData();
    });

    const searchRef = async () => {
      searchData();
    };
    return {
      data,
      loading,
      toast,
      q,
      searchRef,
    };
  },
  methods: {},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
